.samsung-button {
  position: relative;
  width: auto;
  height: 34px;
  float: left;
  padding: 0 20px;
  margin-right: 20px;
  font-weight: bold;
  line-height: 34px;
  text-align: center;
  text-decoration: none;
  background-color: $button-background;
  color: #fff;
  border-radius: 5px;
  // box-shadow: 0px 0px 6px 4px #d1d1d1;
  
  &:hover {
    background-color: #666;
  }
}
